<template>
  <div id="content-products">
    <div class="header">
      <div class="title">All Products</div>
      <div class="btn" @click="showModal">Add New</div>
    </div>
    <table class="table">
      <tr>
        <th class="name">#</th>
        <th class="name">Name</th>
        <th class="category">Category</th>
        <th class="price">Price</th>
        <th class="quantity">Quantity</th>
      </tr>
      <tr v-for="(i, k) in products" :key="i.id">
        <td class="key">{{ k + 1 }}.</td>
        <td class="name">{{ i.name }}</td>
        <td class="category">{{ i.category }}</td>
        <td class="price">{{ i.price }}</td>
        <td class="quantity">{{ i.quantity }}</td>
        <td class="actions">
          <i class="fa-solid fa-ellipsis-vertical"></i>
          <ul class="popup">
            <li @click="activeModalEdit(i)">Edit</li>
            <li @click="handleDeleteProduct(i.id, i.name)">Delete</li>
          </ul>
        </td>
      </tr>
    </table>
    <div class="modal fadeIn" v-if="isShowModal" @click="closeModal">
      <div class="form-group">
        <h2 class="title-form" v-if="isAction === 'create'">Create Product</h2>
        <h2 class="title-form" v-else>Edit Product</h2>
        <div class="wrapper-input">
          <div class="input-group">
            <label for="name">Name</label>
            <input
              id="name"
              type="text"
              placeholder="Name"
              v-model="form.name"
            />
          </div>
          <div class="input-group">
            <label for="category">Category</label>
            <select name="" id="category" v-model="form.category">
              <option value="null" selected>Select</option>
              <option value="head-phone">Head Phone</option>
              <option value="head-band">Head Band</option>
              <option value="head-earpads">Head Earpads</option>
            </select>
          </div>
          <div class="input-group">
            <label for="price">Price</label>
            <input
              id="price"
              type="number"
              placeholder="Price"
              min="1"
              v-model="form.price"
            />
          </div>
          <div class="input-group">
            <label for="quantity">Quantity</label>
            <input
              id="quantity"
              type="number"
              placeholder="Quantity"
              min="1"
              v-model="form.quantity"
            />
          </div>
          <div class="input-group">
            <label for="description">Description</label>
            <textarea name="description" id="description" v-model="form.description" placeholder="Description"></textarea>
          </div>
          <div class="input-group">
            <label for="file">
              <i class="fa-solid fa-image"></i>
              Upload Image Here
            </label>
            <input id="file" type="file" @change="getFile" ref="file" />
            <div class="image">
              <img
                :src="this.file"
                alt="test"
                v-if="this.file"
                ref="prevImage"
                @click="openInputFile"
              />
              <img :src="form.url" alt="" v-else @click="openInputFile" />
            </div>
          </div>
        </div>
        <div
          class="btn"
          v-if="isAction === 'create'"
          @click="handleCreateProduct"
        >
          Create
        </div>
        <div class="btn" v-else @click="handleEditProduct()">Edit</div>
      </div>
      <div class="icon-close" @click="closeModal">
        <i class="icon fa-solid fa-xmark"></i>
      </div>
    </div>
    <div class="toast" v-show="isShowToast" ref="toast">{{ message }}</div>
  </div>
</template>

<script>
import { db } from '@/firebase/config'
import { collection, addDoc, deleteDoc, doc, updateDoc } from "firebase/firestore"
import { getStorage, ref, uploadBytes, getDownloadURL, deleteObject } from "firebase/storage"
import { mapActions, mapState } from 'vuex'

export default {
  name: 'ContentProducts',
  data () {
    return {
      isShowModal: false,
      form: {
        name: null,
        category: null,
        price: null,
        quantity: null,
        description: null
      },
      isShowToast: false,
      message: '',
      isAction: 'create',
      file: null
    }
  },
  created () {
    this.getProducts()
  },
  computed: {
    ...mapState(['products'])
  },
  methods: {
    ...mapActions(['getProducts']),
    showModal () {
      this.isShowModal = true
    },
    closeModal (e) {
      if (e.target.matches('.modal') || e.target.matches('.icon')) {
        this.form = {
          name: null,
          category: null,
          quantity: null,
        }
        this.isShowModal = false
        this.file = null
        this.message = ''
        this.isShowToast = false
        this.isAction = 'create'
      }
    },
    getFile (e) {
      this.file = e.target.files[0]
      const fileReader = new FileReader()
      fileReader.readAsDataURL(this.file)
      fileReader.onload = () => {
        const url = fileReader.result
        this.$refs.prevImage.src = url
      }
    },
    openInputFile () {
      this.$refs.file.click()
    },
    //   Show or hide toast
    handleDisplayToast () {
      this.isShowToast = true
      setTimeout(() => {
        this.message = ''
        this.isShowToast = false
      }, 2000)
    },
    // Add class success or error
    handleAddClassToast (value) {
      if (value == 'success') {
        this.$refs.toast.classList.remove('error')
        this.$refs.toast.classList.add('success')
      } else {
        this.$refs.toast.classList.remove('success')
        this.$refs.toast.classList.add('danger')
      }
    },
    // Create Product
    async handleCreateProduct () {
      if (this.form.name && this.form.category !== 'null' && this.form.price && this.form.quantity && this.file) {
        // Add product
        const docRef = await addDoc(collection(db, 'products'), this.form)
        const id = docRef.id
        const form = this.form
        // Add Image
        const storage = getStorage()
        const storageRef = ref(storage, `imgProducts/${id}`)
        await uploadBytes(storageRef, this.file).then((snapshot) => {
          // Get URL & add URL to product
          getDownloadURL(snapshot.ref)
            .then((downloadURL) => {
              const product = doc(db, "products", id)
              updateDoc(product, { url: downloadURL, ...form })
            })
        })
        this.form = {
          name: null,
          category: null,
          price: null,
          quantity: null,
          description: null
        }
        this.$refs.file.value = null
        this.message = 'Create Success'
        this.handleDisplayToast()
        this.handleAddClassToast('success')
        this.getProducts()

      } else {
        this.message = 'Create Failure'
        this.handleDisplayToast()
        this.handleAddClassToast('error')
      }
    },
    // Delete Product
    async handleDeleteProduct (id, name) {
      let isConfirm = confirm('Are you sure to delete: ' + name)
      if (isConfirm) {
        //   Delete product
        await deleteDoc(doc(db, "products", id))
        // Delete file image
        const storage = getStorage();
        const desertRef = ref(storage, `imgProducts/${id}`)
        await deleteObject(desertRef).then(() => {
          // File deleted successfully
        }).catch((error) => {
          console.log('delete image error:' + error)
        })
        this.message = 'Delete success'
        this.handleDisplayToast()
        this.handleAddClassToast('success')
        this.getProducts()
      }
    },
    // Edit Product 
    activeModalEdit (product) {
      this.isAction = 'edit'
      this.form = { ...product }
      this.isShowModal = true
    },
    async handleEditProduct () {
      const product = doc(db, "products", this.form.id)
      if (this.form.name && this.form.category !== 'null' && this.form.price && this.form.quantity) {
        // Update product
        await updateDoc(product, { id: this.form.id, ...this.form })

        //   Update file Image
        const storage = getStorage()
        const storageRef = ref(storage, `imgProducts/${this.form.id}`)
        if (this.file) {
          await uploadBytes(storageRef, this.file)
        }

        this.form = {
          name: null,
          category: null,
          price: null,
          quantity: null,
          description: null,
        }
        this.file = null
        this.isShowModal = false
        this.isAction = 'create'
        this.getProducts()
        this.message = 'Edit success'
        this.handleDisplayToast()
        this.handleAddClassToast('success')
      } else {
        this.message = 'Edit error'
        this.handleDisplayToast()
        this.handleAddClassToast('error')
      }
    },
  }
}
</script>

<style lang="scss" scoped>
#content-products {
  padding: 32px;
  background: #ffffff;
  border: 1px solid #dfe0eb;
  border-radius: 8px;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .title {
    font-size: 24px;
    line-height: 36px;
  }
  .btn {
    padding: 12px;
  }
}
.table {
  margin-top: 32px;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 12px;
  tr {
    box-shadow: 0px 1px 0px 0px rgba(#dfe0eb, 0.99);
  }
  th {
    padding: 12px 0;
    color: #9fa2b4;
    font-size: 14px;
    line-height: 18px;
    font-weight: 700;
    text-align: left;
  }
  td {
    padding: 24px 0;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    //   border-top: 1.5px solid #dfe0eb;
  }
  .category,
  .quantity,
  .status {
    text-align: center;
  }
  .actions {
    position: relative;
    color: #9fa2b4;
    transition: all ease 0.3s;
    cursor: pointer;
    &:hover {
      color: $color-black;
      .popup {
        display: block;
      }
    }
    .popup {
      display: none;
      position: absolute;
      top: 0%;
      left: 0;
      transform: translate(-50%, 70%);
      border-radius: 4px;
      z-index: 10;
      li {
        padding: 4px;
        background-color: $color-grey-light-1;
        font-size: 14px;
        &:hover {
          background-color: $color-black;
          color: $color-white;
        }
      }
    }
  }
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba($color: $color-black, $alpha: 0.8);
  z-index: 11;
  .icon-close {
    position: absolute;
    top: 20px;
    right: 20px;
    color: $color-white;
    font-size: 36px;
    line-height: 48px;
    cursor: pointer;
  }
  .form-group {
    padding: 24px;
    background-color: $color-white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 4px;
    .title-form {
      color: $color-black;
      font-size: 24px;
      line-height: 36px;
      text-align: center;
    }
    .wrapper-input {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 24px;
      .input-group {
        margin-top: 12px;
        display: flex;
        flex-direction: column;
        grid-gap: 8px;
        label {
          color: $color-grey-dark;
          cursor: pointer;
        }
        input,
        select {
          padding: 12px 24px;
          border: 1px solid $color-grey-dark;
          border-radius: 4px;
          outline: none;
          transition: all ease 0.3s;
          &:focus {
            border-color: $color-blue;
          }
        }
        input[type="number"] {
          &::-webkit-outer-spin-button,
          &::-webkit-inner-spin-button {
            -webkit-appearance: none;
          }
        }
        textarea {
          padding: 6px 12px;
          height: 100%;
          resize: none;
        }
        #file {
          display: none;
        }
        .image {
          //   margin: 12px auto 0;
          width: 100%;
          height: 200px;
          cursor: pointer;
        }
      }
    }
    .btn {
      margin-top: 24px;
      padding: 12px 24px;
    }
  }
}
.toast {
  background-color: $color-white;
  color: $color-white;
  padding: 12px 24px;
  position: fixed;
  right: 10px;
  top: 40%;
  border-radius: 4px;
  animation: toast ease 1s forwards;
  z-index: 12;
  &.success {
    background-color: $color-success;
  }
  &.danger {
    background-color: $color-error;
  }
}
@keyframes toast {
  0% {
    transform: translateX(100%);
  }
  50% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
</style>