<template>
  <div id="sidebar">
    <router-link :to="{ name: 'Admin' }" class="brand">Admin</router-link>
    <ul class="nav">
      <li
        v-for="i in nav"
        :key="i.title"
        :class="[active === i.name ? 'active' : '']"
        @click="handleActive(i.name)"
      >
        <i :class="i.icon"></i>
        <span>{{ i.title }}</span>
      </li>
      <li class="logout" @click="$emit('onLogout')">
        <i class="fa-solid fa-arrow-right-from-bracket"></i>
        <span>Logout</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Sidebar',
  props: ['active'],
  data () {
    return {
      nav: [
        {
          title: 'Dashboard',
          icon: 'fa-solid fa-gauge',
          name: 'dashBoard'
        },
        {
          title: 'Products',
          icon: 'fa-solid fa-shirt',
          name: 'products'
        },

      ]
    }
  },
  methods: {
    handleActive (name) {
      this.$emit('onActive', name)
    }
  }
}
</script>

<style lang="scss" scoped>
#sidebar {
  padding: 32px 0;
  background-color: #363740;
  height: 100vh;
}
.brand {
  margin-bottom: 32px;
  display: block;
  color: $color-white;
  font-size: 32px;
  line-height: 48px;
  font-weight: 700;
  text-align: center;
}
.nav {
  li {
    padding: 24px 32px;
    display: flex;
    align-items: center;
    grid-gap: 24px;
    transition: all ease 0.3s;
    cursor: pointer;
    &.active {
      background-color: $color-black;
      border-right: 4px solid $color-success;
      * {
        color: $color-white;
      }
    }
    &:hover {
      * {
        color: $color-white;
      }
    }
    * {
      color: #a4a6b3;
      font-size: 16px;
      line-height: 24px;
    }
  }
  .logout {
    margin-top: 24px;
    border-top: 1px solid rgba($color: #a4a6b3, $alpha: 0.06);
  }
}
</style>